* {
	box-sizing: border-box;
}

body {
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
		Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #fafafa;
	color: #212121;
	line-height: 1.5;
}

img {
	display: block;
	max-width: 100%;
}

ul {
	padding: 0;
	margin: 0;
	list-style: none;
}

p {
	margin: 0;
	padding: 0;
}

#search-box {
	font: inherit;
	padding: 8px;
	max-width: 280px;
	margin-bottom: 16px;
	outline: none;
	border-radius: 5px;
	color: #000000;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
}

.container {
	max-width: 280px;
	margin: 0 auto;
	padding: 50px 12px;
	text-align: center;
}

.country-list {
	max-width: 280px;
}

.list__item {
	display: flex;
	justify-content: left;
	padding: 8px;
	align-items: center;
	border-radius: 5px;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
}

.list__item:not(:last-child) {
	margin-bottom: 12px;
}

.list__text {
	text-align: left;
	width: 70%;
}

.list__img {
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
}

.item__container {
	margin-right: 10px;
	width: 30%;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 56px;
	height: 56px;
	padding: 1px;
}

.country__container {
	padding: 12px;
	display: flex;
	gap: 20px;
	border-radius: 5px;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
}

.country__name {
	font-size: 20px;
	text-transform: uppercase;
	font-weight: bold;
	margin-bottom: 18px;
}

.img__container {
	margin-bottom: 20px;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
}

.country__property {
	text-align: left;
	font-weight: bold;
}

.property__value {
	font-weight: normal;
}
